"use client";
import React, { useState, useRef, useEffect } from "react";
import { useRouter } from "next/navigation";
import { FaBell, FaBook, FaTrophy, FaGamepad, FaCheckCircle } from "react-icons/fa";

export default function NotificationMenu() {
  const [isOpen, setIsOpen] = useState(false);
  const [notifications, setNotifications] = useState([
    {
      id: 1,
      type: "reviewer",
      title: "New Reviewer Available",
      message: "Introduction to Computer Science reviewer has been uploaded",
      time: "5 minutes ago",
      read: false,
      icon: FaBook,
      link: "/student/reviewers"
    },
    {
      id: 2,
      type: "quiz",
      title: "New Quiz Available",
      message: "Data Structures Quiz is now available to take",
      time: "2 hours ago",
      read: false,
      icon: FaGamepad,
      link: "/student/quizzes"
    },
    {
      id: 3,
      type: "achievement",
      title: "Achievement Unlocked!",
      message: "You've earned the 'Week Warrior' badge for 7-day streak",
      time: "1 day ago",
      read: false,
      icon: FaTrophy,
      link: "/student/dashboard"
    },
    {
      id: 4,
      type: "reviewer",
      title: "Reviewer Updated",
      message: "Database Management reviewer has been updated with new content",
      time: "2 days ago",
      read: true,
      icon: FaBook,
      link: "/student/reviewers"
    },
  ]);

  const menuRef = useRef(null);
  const router = useRouter();

  // Close dropdown when clicking outside
  useEffect(() => {
    const handleClickOutside = (event) => {
      if (menuRef.current && !menuRef.current.contains(event.target)) {
        setIsOpen(false);
      }
    };

    document.addEventListener("mousedown", handleClickOutside);
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, []);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const handleNotificationClick = (notification) => {
    // Mark notification as read
    setNotifications(prev =>
      prev.map(notif =>
        notif.id === notification.id ? { ...notif, read: true } : notif
      )
    );
    
    // Navigate to the respective page
    setIsOpen(false);
    router.push(notification.link);
  };

  const markAllAsRead = () => {
    setNotifications(prev =>
      prev.map(notif => ({ ...notif, read: true }))
    );
  };

  const unreadCount = notifications.filter(n => !n.read).length;

  return (
    <div className="notification-menu-container" ref={menuRef}>
      <button className="notification-btn" onClick={toggleMenu}>
        <FaBell />
        {unreadCount > 0 && (
          <span className="notification-badge">{unreadCount}</span>
        )}
      </button>

      {isOpen && (
        <div className="notification-dropdown">
          <div className="notification-header">
            <h3>Notifications</h3>
            {unreadCount > 0 && (
              <button className="mark-all-read" onClick={markAllAsRead}>
                Mark all as read
              </button>
            )}
          </div>

          <div className="notification-list">
            {notifications.length === 0 ? (
              <div className="no-notifications">
                <FaBell className="empty-icon" />
                <p>No notifications yet</p>
              </div>
            ) : (
              notifications.map((notification) => {
                const IconComponent = notification.icon;
                return (
                  <div
                    key={notification.id}
                    className={`notification-item ${notification.read ? 'read' : 'unread'}`}
                    onClick={() => handleNotificationClick(notification)}
                  >
                    <div className="notification-icon-wrapper">
                      <IconComponent className="notification-icon" />
                    </div>
                    <div className="notification-content">
                      <div className="notification-title">{notification.title}</div>
                      <div className="notification-message">{notification.message}</div>
                      <div className="notification-time">{notification.time}</div>
                    </div>
                    {!notification.read && (
                      <div className="unread-indicator"></div>
                    )}
                  </div>
                );
              })
            )}
          </div>

          {notifications.length > 0 && (
            <div className="notification-footer">
              <button className="view-all-btn">View All Notifications</button>
            </div>
          )}
        </div>
      )}
    </div>
  );
}
